* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --hovercolor: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header__logo {
  max-height: 40px;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

a {
  text-decoration: none;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__item {
  text-align: center;
  text-transform: uppercase;
  margin-right: 20px;
  display: inline-block;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-align: center;
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--hovercolor);
}

.is-active {
  color: var(--hovercolor);
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: var(--hovercolor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
